import React, { useState, useContext } from "react";
import { Form, Select, Table } from "antd";
import UserContext from "../../../context/user/userContext";
import { useEffect } from "react";
const { Option } = Select;

const layout = {
  labelCol: { span: 8 },
  wrapperCol: { span: 16 },
};

const dataSourceDefault = [];
const columnsDefault = [
  {
    title: "Ti khon gi",
    dataIndex: "depositor",
    key: "depositor",
  },
  {
    title: "Ngi nhn",
    dataIndex: "receiver",
    key: "receiver",
  },
  {
    title: "S tin",
    dataIndex: "amount",
    key: "amount",
  },
  {
    title: "Thi gian",
    dataIndex: "timestamp",
    key: "timestamp",
  },
  {
    title: "Ghi ch",
    dataIndex: "note",
    key: "note",
  },
];
const types = [
  { title: "Chuyn tin", key: "transfer" },
  {
    title: "Nhn tin",
    key: "receiver",
  },
  {
    title: "Tr n",
    key: "debt",
  },
];
const TransactionsPage = (props) => {
  const userContext = useContext(UserContext);
  const { loading, transactions, accountsOwner, getTransactions } = userContext;
  const [dataSource, setDataSource] = useState(dataSourceDefault);
  const [columns, setColumns] = useState(columnsDefault);
  const [selectedAccount, setSelectedAccount] = useState();
  const [curType, setCurType] = useState(types[0]);

  const onAccountSelected = (selectedAccount) => {
    console.log("finish ", selectedAccount);
    setSelectedAccount(selectedAccount);
    getTransactions(selectedAccount);
  };

  useEffect(() => {
    renderListTransactions(curType);
  }, [transactions]);
  const onAccountFinishFailed = () => {};

  const Types = (types) => {
    console.log(types);
    return types.map((item) => (
      <Option title={item.title} value={item.key} key={item.key}>
        {" "}
        {item.title}
      </Option>
    ));
  };

  const accounts = (accounts = []) =>
    //<Option></Option>
    accounts.map((acc, i) => (
      <Option value={acc.account_number} key={i}>
        {acc.account_number}
      </Option>
    ));

  const onSelectType = (type) => {
    renderListTransactions(type);
    setCurType(type);
  };

  const renderListTransactions = (type) => {
    switch (type) {
      case "receiver":
        setDataSource(validateTransactionsData(transactions.receivers));
        break;
      case "transfer":
        setDataSource(validateTransactionsData(transactions.transfers));
        break;
      case "debt":
        setDataSource(validateTransactionsData(transactions.debts));
        break;
      default:
        break;
    }
  };

  const validateTransactionsData = (list) => {
    var newData = list;

    for (let index = 0; index < newData.length; index++) {
      const element = newData[index];
      const time = new Date(element.timestamp);
      const timeString = time.toDateString();
      newData[index] = { ...element, key: element.id, timestamp: timeString };
    }
    return newData;
  };
  return (
    <div>
      <Form
        {...layout}
        name="basic"
        initialValues={{ remember: true }}
        onFinishFailed={onAccountFinishFailed}
      >
        <Form.Item
          label="Ti khon"
          className="border-bottom border-light p-3"
          name="SelectedAccount"
        >
          <Select onSelect={onAccountSelected} placeholder="Chn ti khon">
            {accounts(accountsOwner)}
          </Select>
        </Form.Item>
        <Form.Item
          label="Loi giao dch"
          className="border-bottom border-light p-3"
          name="selectType"
        >
          <Select
            onSelect={onSelectType}
            placeholder="Chn"
            disabled={selectedAccount ? false : true}
          >
            {Types(types)}
          </Select>
        </Form.Item>
      </Form>
      <Table dataSource={dataSource} columns={columns} loading={loading} />
    </div>
  );
};

export default TransactionsPage;
